/* General Styles */
.auction-room {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    background: linear-gradient(to bottom, #f5f5f5, #eaeaea);
    min-height: 100vh;
    font-family: 'Helvetica Neue', Arial, sans-serif;
    overflow: hidden; /* Ensure no scroll bars appear */
}

.auction-item {
    text-align: center;
    margin-bottom: 30px;
    border-radius: 15px;
    overflow: hidden;
    background: linear-gradient(145deg, #fff, #f0f0f0);
    box-shadow: 0 10px 20px rgba(0,0,0,0.2);
    padding: 20px;
    transition: box-shadow 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.auction-item:hover {
    box-shadow: 0 15px 30px rgba(0,0,0,0.3);
    transform: scale(1.02);
}

.item-image img {
    max-width: 100%;
    max-height: 400px;
    object-fit: cover;
    border-radius: 10px;
    transition: transform 0.3s ease-in-out;
}

.item-image img:hover {
    transform: scale(1.1);
}

.current-price, .end-time {
    font-size: 1.6em;
    font-weight: bold;
    color: #333;
    margin: 20px 0;
}

.bidding-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
}

.bidding-section input {
    width: 240px;
    padding: 14px;
    margin: 14px 0;
    font-size: 1.4em;
    text-align: center;
    border: 2px solid #007bff;
    border-radius: 6px;
    transition: border-color 0.3s ease-in-out;
}

.bidding-section input:focus {
    border-color: #0056b3;
    outline: none;
}

.bidding-section button {
    padding: 14px 28px;
    font-size: 1.4em;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out, transform 0.2s ease-in-out;
    box-shadow: 0 6px 12px rgba(0,0,0,0.2);
}

.bidding-section button:hover {
    background-color: #0056b3;
    transform: scale(1.02);
}

.bidding-section button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}

.feedback-section {
    position: fixed;
    bottom: 10px;
    left: 10px;
    width: 300px;
    max-height: 400px;
    overflow-y: auto;
    background: rgb(252, 247, 247, 0.7);
    color: white;
    border-radius: 8px;
    padding: 10px;
    box-shadow: 0 4px 8px rgba(0,0,0,0.3);
}

.feedback-message, .feedback-error {
    background-color: rgba(0, 0, 0, 0.8);
    border: 1px solid #444;
    padding: 10px;
    margin-bottom: 6px;
    border-radius: 4px;
    animation: fadeIn 0.5s ease-in-out;
    font-size: 0.9em; /* Smaller font size for feedback */
}

.feedback-error {
    background-color: #dc3545;
    border-color: #c82333;
    color: #fff;
}

/* Animation for feedback messages */
@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

/* Countdown Timer Animation */
.countdown {
    font-size: 1.8em;
    font-weight: bold;
    color: #dc3545;
    animation: pulse 1s infinite;
}

@keyframes pulse {
    0% {
        transform: scale(1);
        opacity: 1;
    }
    50% {
        transform: scale(1.05);
        opacity: 0.8;
    }
    100% {
        transform: scale(1);
        opacity: 1;
    }
}

/* Responsive Design */
@media (max-width: 768px) {
    .bidding-section input, .bidding-section button {
        width: 100%;
        font-size: 1.2em;
    }

    .current-price, .end-time {
        font-size: 1.4em;
    }

    .feedback-section {
        width: 100%;
    }
}
